.about-us {
  width: 100%;
  padding: 100px 20px;
  background: #F6F6F8;
}

.about-us__info {
  position: relative;
  grid-area: lf;

  height: min-content;
  display: grid;
  grid-template-rows: min-content min-content;
  grid-row-gap: 20px;

  @media (max-width: @tablet-width) {
    height: 140px;
    overflow: hidden;
    border-radius: 0 0 15px 15px;

    &--open {
      height: min-content;

      &::after {
        display: none;
      }
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 45px;
      background: rgb(120,118,143);
      background: linear-gradient(0deg, rgba(120,118,143,0.4) 0%, rgba(255,255,255,0) 100%);
    }
  }
}

.about-us__history {
  grid-area: hs;

  display: grid;
  grid-template-rows: repeat(3, min-content);
  grid-row-gap: 20px;

  padding: 40px 20px;
  background-color: #0068ab;
  color: @standart-white;

  box-shadow:  14px 14px 28px #929292,
  -14px -14px 28px #f5f5f5;
  border-radius: 30px;

  @media (max-width: @tablet-width) {
    padding: 20px;
    grid-template-areas:
    "tl tl tl"
    "tx tx im"
    ;
    grid-row-gap: 10px;
    grid-column-gap: 10px;
  }

  @media (max-width: @tablet-width) {
    // grid-template-areas:
    // "tl tl tl"
    // "im im im"
    // "tx tx tx"
    // ;
    display: block;
  }
}

.about-us__mission {
  grid-area: tf;
}

.about-us__politic {
  grid-area: pl;
}

.about-us__title {
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  font-size: 36px;
  line-height: 53px;
  color: #323232;

  &--history {
    color: @standart-white;

    @media (max-width: @tablet-width) {
      grid-area: tl;
    }
  }
}

.about-us__text {
  font-family: 'Rubik', sans-serif;
  font-size: 16px;
  line-height: 25px;
}

.about-us__img {
  width: 200px;
  height: 200px;
  background-color: #929292;

  @media (max-width: @tablet-width) {
    grid-area: im;
  }

  @media (max-width: @tablet-width) {
    width: 150px;
    height: 150px;
    float: left;
    margin: 0 8px 0 0;
  }
}

.about-us__quotes {
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;

  @media (max-width: @tablet-width) {
    grid-area: tx;
  }
}

.about-us__quotes-autor {
  font-family: 'Open Sans', sans-serif;
  text-transform: initial;
  display: block ;
  margin-top: 10px;
}

.mission-list {
  padding-left: 20px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content;
  grid-row-gap: 10px;
}

.mission-list__item {
  font-family: 'Rubik', sans-serif;
}

.about-us__show-more-btn {
  position: absolute;
  width: 40px;
  height: 40px;
  padding: 5px;
  background-color: transparent;
  border: none;
  top: 10px;
  right: 5px;
  //transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.about-us__show-more-icon {
  transform: rotate(90deg);
  width: 100%;
  height: 100%;

  &--close {
    transform: rotate(-90deg);
  }
}
